.main-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
  padding-top: 75px;
  width: 420px;
  text-align: center;
  img {
    padding-bottom: 48px;
  }
  h2 {
    font-weight: 600;
    font-size: 32px;
    line-height: 44px;
    color: #000000;
  }
  p {
    font-size: 16px;
    line-height: 20px;
    color: #999999;
    padding-top: 8px;
    padding-bottom: 30px;
  }
  .buttons {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    button {
      background: #4e74f5;
      border-radius: 64px;
      height: 64px;
      width: 100%;
      font-weight: 700;
      font-size: 16px;
      line-height: 13px;
      color: #ffffff;
      margin-top: 30px;
      margin-bottom: 24px;
      &:hover {
        background: #748feb;
      }
    }
    h6 {
      font-size: 14px;
      line-height: 24px;
      color: #999999;
    }
    a {
      width: 100%;
    }
  }
  .auth-form {
    width: 100%;
    input {
      border: 1px solid #e6e6e6;
      box-sizing: border-box;
      border-radius: 12px;
      width: 100%;
      height: 56px;
      padding-left: 20px;
      padding-right: 20px;
      &::placeholder {
        font-size: 14px;
        line-height: 24px;
        color: #e6e6e6;
      }
      &:focus {
        border: 1px solid #4e74f5;
      }
    }
  }
}
@media (max-width: 630px) {
  .main-section {
    width: 70%;
    h2 {
      font-size: 24px;
      line-height: 30px;
    }
    p {
      font-size: 14px;
    }
    .buttons {
      .submit {
        width: 100%;
      }
    }
  }
}
